
import * as types from './mutation-types'

export default {
	[types.GET_SIDEBAR_LOADING_STATUS] (state, status) {
		state.isSidebarLoading = status
	},
	[types.GET_SIDEBAR_DIALOG_STATUS] (state, status) {
		state.isDialog = status
	},
	[types.SET_GOODS_LIST_TAB] (state, val) {
		state.goodsListTab = val
	}
}
