import Vue from 'vue'
import Router from 'vue-router'
// import Home from './views/Home.vue'
import store from './store'
Vue.use(Router)

const rotuer = new Router({
//   mode: 'history',
  linkActiveClass: 'active',
  linkExactActiveClass: 'active',
//   base: process.env.BASE_URL,
  routes: [
	{
		path: '/',
		redirect: '/home'
	},
    {
		path: '/home',
		component: () => import(/* webpackChunkName: 'layouts' */ './components/layouts/index.vue'),
		children: [
			{
				path: '/',
				meta: {
					title: '首页'
				},
				component: () => import(/* webpackChunkName: 'layouts' */ './views/home/index.vue')
			},
			{
				path: '/about',
				meta: {
					title: '关于我'
				},
				component: () => import(/* webpackChunkName: 'layouts' */ './views/about/index.vue')
			},
			{
				path: '/desire',
				meta: {
					title: '求职意向'
				},
				component: () => import(/* webpackChunkName: 'layouts' */ './views/desire/index.vue')
			},
			{
				path: '/contact',
				meta: {
					title: '联系我'
				},
				component: () => import(/* webpackChunkName: 'layouts' */ './views/contact/index.vue')
			}
		]
    },
    {
		path: '/about',
		name: 'about',
		meta: {
			title: `${store.state.title} - 首页`
		},
		// route level code-splitting
		// this generates a separate chunk (about.[hash].js) for this route
		// which is lazy-loaded when the route is visited.
		component: () => import(/* webpackChunkName: "about" */ './views/About.vue')
    }
  ]
})
rotuer.beforeEach((to, from, next) => {
	if (to.meta.title) { // 如果设置标题，拦截后设置标题
		document.title = to.meta.title
	}
	next()
  })

export default rotuer
